import FetchError from '../FetchError';
import {
    getResponseInterceptorHandlers,
    getErrorHandlers,
} from '../interceptors';
import {
    ABORT_ERROR,
    FETCH_ERROR,
    RESPONSE_ERROR,
    TIMEDOUT_ERROR,
} from './constants';
import { storeInCache } from './responseCache';

const isJSONResponse = (contentType) => {
    return (
        contentType &&
        (contentType.indexOf('application/json') !== -1 ||
            contentType.indexOf('application/problem+json') !== -1)
    );
};

const isTextResponse = (contentType) => {
    return contentType && contentType.indexOf('text/') === 0;
};

const handleResponse = async (httpResponse, fetchConfig) => {
    let responseData;

    let response = httpResponse;

    const { options } = fetchConfig;
    const {
        isCacheable = false,
        shouldIncludeResponseAttributes = false,
        method,
    } = options;

    const responseInterceptorHandlers = getResponseInterceptorHandlers();

    for (let i = 0; i < responseInterceptorHandlers.length; i += 1) {
        response =
            // eslint-disable-next-line no-await-in-loop
            (await responseInterceptorHandlers[i].interceptorHandler(
                response,
                fetchConfig,
            )) ?? response;

        if (!(response instanceof Response)) {
            const blob = new Blob([JSON.stringify(response)], {
                type: 'application/json',
            });

            const headers = { status: 200, statusText: 'OK' };
            response = new Response(blob, headers);
        }
    }

    const { headers: responseHeaders = {}, status } = response;

    const contentType = responseHeaders.get('content-type');

    if (method !== 'HEAD') {
        if (isJSONResponse(contentType)) {
            responseData = await response.json();
        } else if (isTextResponse(contentType)) {
            responseData = await response.text();
        } else {
            responseData = response;
        }
    }

    if (shouldIncludeResponseAttributes) {
        const { statusText } = response;
        responseData = {
            data: responseData,
            status,
            statusText,
            headers: Object.fromEntries(responseHeaders.entries()),
        };
    }

    if (!response.ok) {
        throw new FetchError({
            type: RESPONSE_ERROR,
            response,
            responseData,
            status,
        });
    }

    if (isCacheable) {
        storeInCache(fetchConfig, responseData);
    }

    return responseData;
};

export const handleError = (err, requestTimedout) => {
    const errorHandlers = getErrorHandlers();
    const eventParam = { error: err, preventDefault: false };

    for (let i = 0; i < errorHandlers.length; i += 1) {
        const { errorHandler } = errorHandlers[i];

        errorHandler(eventParam);

        if (eventParam.preventDefault === true) {
            return;
        }
    }

    if (err instanceof FetchError) {
        throw err;
    }

    if (err.name === 'AbortError') {
        if (requestTimedout) {
            throw new FetchError({
                type: TIMEDOUT_ERROR,
            });
        } else {
            throw new FetchError({
                type: ABORT_ERROR,
            });
        }
    }

    throw new FetchError({
        type: FETCH_ERROR,
    });
};

export default handleResponse;
