import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { LoadingDots } from '@Green-Dot-Corporation/eureka-ui-icons';
import { Callout } from '@Green-Dot-Corporation/eureka-ui-callouts';

// Styles
import './styles/loading-state.scss';

class LoadingState extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        title: PropTypes.string,
        msg: PropTypes.string,
        renderIcon: PropTypes.func,
    };

    render() {
        const { contextCls, renderIcon, ...rest } = this.props;
        const iconRenderer = renderIcon || this.renderIcon;

        return (
            <Callout
                {...rest}
                contextCls={cx(this.baseCls, contextCls)}
                renderIcon={iconRenderer}
            />
        );
    }

    renderIcon = (iconCls) => {
        return <LoadingDots contextCls={iconCls} />;
    };

    baseCls = 'eureka-loading-state';
}

export default LoadingState;
